
.good {
  display: flex;
  position: absolute;
  width: 100%;
  top: 1.74rem;
  bottom: 0.46rem;
  overflow: hidden;
}
.good .menu-wrapper {
  flex: 0 0 0.8rem;
  width: 0.8rem;
  background: #f3f5f7;
}
.good .menu-wrapper .menu-item {
  display: table;
  width: 0.68rem;
  height: 0.54rem;
  line-height: 0.14rem;
  padding: 0 0.06rem;
}
.good .menu-wrapper .menu-item.current {
  position: relative;
  z-index: 10;
  margin-top: -0.01rem;
  background: #fff;
  font-weight: 700;
}
.good .menu-wrapper .menu-item.current .text:after {
  display: none;
}
.good .menu-wrapper .menu-item .icon {
  display: inline-block;
  width: 0.12rem;
  height: 0.12rem;
  vertical-align: top;
  margin-right: 0.04rem;
  background-size: 0.12rem 0.12rem;
  background-repeat: no-repeat;
}
.good .menu-wrapper .menu-item .text {
  display: table-cell;
  width: 56rem;
  vertical-align: middle;
  position: relative;
  font-size: 0.135rem;
  color: #333333;
}
.good .menu-wrapper .menu-item .text:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  width: 100%;
  content: '';
}
.good .foods-wrapper {
  flex: 1;
  width: 2rem;
}
.good .foods-wrapper .food-list .title {
  padding-left: 0.14rem;
  height: 0.26rem;
  line-height: 0.26rem;
  border-left: 0.02rem solid #d9dde1;
  font-size: 0.12rem;
  font-weight: 700;
  color: #666;
  background: #f3f5f7;
}
.good .foods-wrapper .food-list .text {
  color: #93999f;
  font-size: 0.12rem;
}
.good .foods-wrapper .food-list .food-item {
  display: flex;
  margin: 0.05rem;
  padding-bottom: 0.18rem;
  position: relative;
}
.good .foods-wrapper .food-list .food-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  width: 100%;
  content: '';
}
.good .foods-wrapper .food-list .food-item:last-child {
  margin-bottom: 0rem;
}
.good .foods-wrapper .food-list .food-item:last-child:after {
  display: none;
}
.good .foods-wrapper .food-list .food-item .icon {
  flex: 0 0 0.75rem;
  margin-right: 0.1rem;
  display: flex;
  align-items: flex-start;
}
.good .foods-wrapper .food-list .food-item .icon img {
  width: 0.75rem;
}
.good .foods-wrapper .food-list .food-item .content {
  flex: 1;
}
.good .foods-wrapper .food-list .food-item .content .name {
  margin: 0.02rem 0 0.08rem 0;
  font-size: 0.14rem;
  line-height: 0.14rem;
  color: #333;
  font-weight: 500;
}
.good .foods-wrapper .food-list .food-item .content .desc,
.good .foods-wrapper .food-list .food-item .content .extra {
  font-size: 0.1rem;
  line-height: 0.1rem;
  color: #93999f;
}
.good .foods-wrapper .food-list .food-item .content .desc {
  margin-bottom: 0.08rem;
  line-height: 0.12rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.good .foods-wrapper .food-list .food-item .content .extra .count {
  margin-right: 0.12rem;
}
.good .foods-wrapper .food-list .food-item .content .discount {
  margin-top: 0.05rem;
  width: 1.2rem;
  height: 0.14rem;
  display: flex;
  border: 0.5px solid #ff3c15;
  font-size: 0.12rem;
  align-items: center;
}
.good .foods-wrapper .food-list .food-item .content .discount .nummber {
  width: 0.35rem;
  font-size: 0.12rem;
  color: #ffffff;
  background-color: #ff3c15;
  padding: 0.01rem 0.02rem;
}
.good .foods-wrapper .food-list .food-item .content .discount .count {
  color: #ff3c15;
  padding: 0.01rem 0rem;
}
.good .foods-wrapper .food-list .food-item .content .price {
  font-weight: 700;
  line-height: 0.24rem;
  font-size: 0.1rem;
}
.good .foods-wrapper .food-list .food-item .content .price .now {
  margin-right: 0.08rem;
  font-size: 0.14rem;
  color: #f01414;
}
.good .foods-wrapper .food-list .food-item .content .price .old {
  font-size: 0.1rem;
  color: #93999f;
  text-decoration: line-through;
}
.good .foods-wrapper .food-list .food-item .content .cartControl-wrapper {
  position: absolute;
  right: 0;
  bottom: 0.12rem;
}
.good .ball {
  position: fixed;
  left: 0.3rem;
  bottom: 0.3rem;
  z-index: 49;
}
.good .ball .inner {
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 50%;
  background: #00a0dc;
  transition: all 0.4s linear;
}



