
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0.48rem;
  z-index: 30;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.1rem;
}
.food.fade-enter-active,
.food.fade-leave-active {
  transition: all 0.2s linear;
  transform: translate3d(0, 0, 0);
}
.food.fade-enter,
.food.fade-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.food .food-marker {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
}
.food .food-content {
  position: relative;
  z-index: 13;
  width: 3.2rem;
  height: 4.28rem;
  background: #ffffff;
  border-radius: 0.05rem;
}
.food .food-content .image-header {
  width: 3.2rem;
  height: 3.2rem;
  position: relative;
}
.food .food-content .image-header .description {
  font-size: 0.12rem;
  color: #ddd;
  letter-spacing: 0;
  line-height: 0.12rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 0.1rem 0.1rem;
}
.food .food-content .image-header .img {
  width: 3.2rem;
  height: 3.2rem;
}
.food .food-content .food-info {
  position: relative;
  padding: 0.15rem 0.15rem 0;
}
.food .food-content .food-info .name {
  margin: 0.02rem 0 0.08rem 0;
  font-size: 0.14rem;
  line-height: 0.14rem;
  color: #333;
  font-weight: 500;
}
.food .food-content .food-info .extra {
  font-size: 0.1rem;
  line-height: 0.1rem;
  color: #93999f;
}
.food .food-content .food-info .discount {
  margin-top: 0.05rem;
  width: 1.2rem;
  height: 0.14rem;
  display: flex;
  border: 0.5px solid #ff3c15;
  font-size: 0.12rem;
  align-items: center;
}
.food .food-content .food-info .discount .nummber {
  width: 0.35rem;
  font-size: 0.12rem;
  color: #ffffff;
  background-color: #ff3c15;
  padding: 0.01rem 0.02rem;
}
.food .food-content .food-info .discount .count {
  color: #ff3c15;
  padding: 0.01rem 0rem;
}
.food .food-content .food-info .price {
  font-weight: 700;
  line-height: 0.24rem;
  font-size: 0.1rem;
}
.food .food-content .food-info .price .now {
  margin-right: 0.08rem;
  font-size: 0.14rem;
  color: #f01414;
}
.food .food-content .food-info .price .old {
  font-size: 0.1rem;
  color: #93999f;
  text-decoration: line-through;
}
.food .food-content .food-info .cartControl-wrapper {
  position: absolute;
  right: 0;
  bottom: 0.12rem;
}



