
@keyframes mymove {
0% {
    transform: scale(1);
}
25% {
    transform: scale(0.8);
}
50% {
    transform: scale(1.1);
}
75% {
    transform: scale(0.9);
}
100% {
    transform: scale(1);
}
}
@-moz-keyframes mymove {
0% {
    transform: scale(1);
}
25% {
    transform: scale(0.8);
}
50% {
    transform: scale(1.1);
}
75% {
    transform: scale(0.9);
}
100% {
    transform: scale(1);
}
}
@-webkit-keyframes mymove {
0% {
    transform: scale(1);
}
25% {
    transform: scale(0.8);
}
50% {
    transform: scale(1.1);
}
75% {
    transform: scale(0.9);
}
100% {
    transform: scale(1);
}
}
@-o-keyframes mymove {
0% {
    transform: scale(1);
}
25% {
    transform: scale(0.8);
}
50% {
    transform: scale(1.1);
}
75% {
    transform: scale(0.9);
}
100% {
    transform: scale(1);
}
}
.shopCart {
  position: fixed;
  left: 0rem;
  bottom: 0rem;
  z-index: 50;
  width: 100%;
  height: 0.48rem;
}
.shopCart .content {
  display: flex;
  background: #141d27;
}
.shopCart .content .content-left {
  flex: 1;
  display: flex;
}
.shopCart .content .content-left .logo-wrapper {
  display: inline-block;
  position: relative;
  top: -0.1rem;
  margin: 0 0.12rem;
  padding: 0.06rem;
  width: 0.56rem;
  height: 0.56rem;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  background: #141d27;
}
.shopCart .content .content-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.24rem;
  height: 0.24rem;
  line-height: 0.24rem;
  text-align: center;
  border-radius: 0.16rem;
  font-size: 0.09rem;
  font-weight: 700;
  color: #ffffff;
  background: #f01414;
  box-shadow: 0 0.04rem 0.08rem 0 rgba(0, 0, 0, 0.4);
}
.shopCart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  text-align: center;
  border-radius: 50%;
  background: #2b343c;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shopCart .content .content-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shopCart .content .content-left .logo-wrapper .logo.move_in_cart {
  animation: mymove 0.5s ease-in-out;
}
.shopCart .content .content-left .logo-wrapper .logo .icon-gouwuche {
  line-height: 0.44rem;
  font-size: 0.24rem;
  color: #80858a;
}
.shopCart .content .content-left .logo-wrapper .logo .icon-gouwuche.highlight {
  color: #fff;
}
.shopCart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 0.12rem;
  line-height: 0.24rem;
  padding-right: 0.12rem;
  box-sizing: border-box;
  border-right: 0.01rem solid rgba(255, 255, 255, 0.1);
  font-size: 0.16rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.4);
}
.shopCart .content .content-left .price.highlight {
  color: #ffffff;
}
.shopCart .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  line-height: 0.24rem;
  margin-left: 0.12rem;
  margin-top: 0.12rem;
  color: rgba(255, 255, 255, 0.4);
  font-size: 0.1rem;
}
.shopCart .content .content-right {
  flex: 0 0 1.05rem;
  width: 1.05rem;
}
.shopCart .content .content-right .pay {
  height: 0.48rem;
  line-height: 0.48rem;
  text-align: center;
  font-size: 0.12rem;
  color: rgba(255, 255, 255, 0.4);
  font-weight: 700;
  background: #2b333b;
}
.shopCart .content .content-right .pay.not-enough {
  background: #2b333b;
}
.shopCart .content .content-right .pay.enough {
  background: #00b43c;
  color: #ffffff;
}
.shopCart .shopcart-list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  transform: translate3d(0, -100%, 0);
}
.shopCart .shopcart-list.fade-enter-active,
.shopCart .shopcart-list.fade-leave-active {
  transition: all 0.5s;
  transform: translate3d(0, -100%, 0);
}
.shopCart .shopcart-list.fade-enter,
.shopCart .shopcart-list.fade-leave-active {
  transform: translate3d(0, 0, 0);
}
.shopCart .shopcart-list .list-header {
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0 0.18rem;
  background: #f3f5f7;
  border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
}
.shopCart .shopcart-list .list-header .title {
  float: left;
  font-size: 0.14rem;
  color: #07111b;
}
.shopCart .shopcart-list .list-header .empty {
  float: right;
  font-size: 0.12rem;
  color: #00a0dc;
}
.shopCart .shopcart-list .list-content {
  padding: 0 0.18rem;
  max-height: 2.17rem;
  overflow: hidden;
  background: #ffffff;
}
.shopCart .shopcart-list .list-content .shopcart-food {
  position: relative;
  box-sizing: border-box;
  line-height: 0rem;
}
.shopCart .shopcart-list .list-content .shopcart-food .name {
  line-height: 0.24rem;
  font-size: 0.14rem;
  color: #07111b;
}
.shopCart .shopcart-list .list-content .shopcart-food .price {
  position: absolute;
  right: 0.9rem;
  bottom: 0.02rem;
  line-height: 0.24rem;
  font-size: 0.14rem;
  font-weight: 700;
  color: #f01414;
}
.shopCart .shopcart-list .list-content .shopcart-food .cartControl-wrapper {
  position: absolute;
  right: 0;
  bottom: -0.04rem;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  backdrop-filter: blur(0.1rem);
  -webkit-backdrop-filter: blur(0.1rem);
  opacity: 1;
  background: rgba(7, 17, 27, 0.6);
}
.list-mask.fade-enter-active,
.list-mask.fade-leave-active {
  opacity: 1;
  transition: all 0.5s;
  background: rgba(7, 17, 27, 0.6);
}
.list-mask.fade-enter,
.list-mask.fade-leave-active {
  opacity: 0;
  background: rgba(7, 17, 27, 0);
}



